.card-deck .card {
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.card-deck .card:hover {
  opacity: 0.8;
  cursor: pointer;
}
.card-deck .card p:hover,
.card-deck .card a:hover {
  text-decoration: none;
}

.card-deck .card .card-text {
  word-wrap: break-word;
  overflow: hidden;
}
.card-text {
